<template lang="pug">
  #app
    .ts.centered
    .ts.action.fluid.input.raised#input
      input(type="text", v-model="deliveryId", placeholder="輸入單號")
      button.ts.button(@click='buttonClicked') 查詢
    .ts.primary.segment
      p 目前支援東風貨運、黑貓宅急便。
    response(:response="deliveryId", v-if="hasRespond")
</template>

<script>
import Response from './components/Response'
import axios from 'axios'

export default {
  name: 'app',
  components: {
    Response
  },
  data () {
    return {
      deliveryId: 'testtt',
      hasRespond: false,
      responseData: null
    }
  },
  methods: {
    buttonClicked () {
      // axios.post('127.0.0.1:8000/testData/', {
      //   deliveryId: this.deliveryId
      // })
      //   .then((response) => {
      //     this.responseData = response.data
      //   })
      //   .catch((error) => {
      //     console.log(error)
      //   })
      axios({
        method: 'post',
        url: 'http://127.0.0.1:8000/testPOST/',
        responseType: 'json',
        data: {
          deliveryId: this.deliveryId
        }
      })
      this.hasRespond = true
    }
  }
}
</script>

<style scope>

#input {
  padding-top: 30px;
  padding-bottom: 20px;
}

</style>



